import { useState, useEffect,useContext } from 'react'
import secureLocalStorage from 'react-secure-storage'
import { contextVar } from '../ContextVar'
import {BiUserCircle} from 'react-icons/bi'
import { useNavigate } from 'react-router-dom'
function ImageCard(props) {
  const { userName, roles,setuserName,setroles } = useContext(contextVar)
  const navigate  = useNavigate()

  useEffect(() => {
    ///*** setting global data ***\\\\
    let lUser = userName==''? window.localStorage.getItem('userName'):userName
    // let lRoles = roles==''? JSON.parse(window.localStorage.getItem('roles')):roles
    console.log('username at sidebar...', lUser)
    setuserName(lUser)
    // setroles(lRoles)

  }, [])

  return (
    <>
      <div className="text-center px-1" >
       <div className='w-full h-20'>
         <div><BiUserCircle className="inline text-4xl shadow-lg rounded-full hover:scale-110 cursor-pointer" /></div>
         <div> <span onClick={()=>navigate('/editProfile')} className='text-xs px-2 font-semibold bg-green-500 rounded-lg text-white shadow-lg cursor-pointer border border-white hover:scale-110'>Edit</span> </div>
         </div>
        <p className='text-sm text-gray-500 font-semibold text-sm' style={props.sideBarStatus ? { 'display': 'none' } : { 'display': 'block' }}>{userName}</p>
        {roles !='' && roles?.map((role) => (
          <p className='text-xs' style={props.sideBarStatus ? { 'display': 'none' } : { 'display': 'block' }}>{'Admin'}</p>
        ))}
      </div>
    </>
  )
}

export default ImageCard
