import { useState, useEffect, useContext } from 'react'
import { useLocation, useNavigate } from 'react-router-dom'
import ImageCard from './ImageCard';
import { connect } from "react-redux";
import { FcFlowChart } from 'react-icons/fc'
import CollapseMenu from './CollapseMenu';
import { contextVar } from '../ContextVar';

function Sidebar(props) {

  // const [menuList, setmenuList] = useState([{menuName:'mark',menuPath:'shark'}])
  // const { menuList, setmenuList } = useContext(contextVar)
  const [staticMenuList, setstaticMenuList] = useState('')
  const [subMenuName, setsubMenuName] = useState('')
  const [subMenuStatus, setsubMenuStatus] = useState(false)
  //** Initial menu list of all available modules *\\
  const initialMenu = [
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Dashboard', menuPath: '/landingDashboard', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Property', menuPath: '/propertyDashboard', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Trade', menuPath: '/view-trade', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Water', menuPath: '/view-water', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'View Profile', menuPath: '/editProfile', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Payment History', menuPath: '/allTranscationList', subMenu: [] },

  ]

  //** Property menu list *\\
  const propertyMenu = [
    // { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Dashboard', menuPath: '/landingDashboard', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Property Dashboard', menuPath: '/propertyDashboard', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Holding Apply', menuPath: '/safform/new/0', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Payment History', menuPath: '/PropertyAllTranscationList', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Tax Calculator', menuPath: '/tax-cal', subMenu: [] },
  ]

  //** Trade menu list *\\
  const tradeMenu = [
    // { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Dashboard', menuPath: '/landingDashboard', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Trade Dashboard', menuPath: '/view-trade', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Trade New License', menuPath: '/trade-new-apply', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Trade Applications', menuPath: '/track-trade', subMenu: [] },
    // { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Trade Renewal', menuPath: '/trade-renewal-list', subMenu: [] },
    // { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Trade Amendment', menuPath: '/trade-amendment-list', subMenu: [] },
    // { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Trade Surrender', menuPath: '/trade-surrender-list', subMenu: [] },
  ]

  //** Water menu list *\\
  const waterMenu = [
    // { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Dashboard', menuPath: '/landingDashboard', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Water Dashboard', menuPath: '/view-water', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Water Apply', menuPath: '/water-apply', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Water Applications', menuPath: '/track-water', subMenu: [] },
  ]


  useEffect(() => {
    setstaticMenuList(initialMenu)
  }, [])


  const navigate = useNavigate();
  const location = useLocation()
  if (location.pathname == '/' || location.pathname == '/login' || location.pathname == '/register' || location.pathname == '/registerSuccess') {
    return
  }




  //function which is chaning menulist according to module
  const menuChange = (menuType) => {
    console.log('inside menuChange.....', menuType)
    if (menuType == 'Back') {
      setstaticMenuList(initialMenu)
      setsubMenuName('')
      setsubMenuStatus(false)
      navigate('/landingDashboard')
    }

    if (menuType == 'Property') {
      //changing the menu according to module
      setstaticMenuList(propertyMenu)
      //setting module name to show as titile
      setsubMenuName(menuType)
      //setting state to manage show / hide title at top
      setsubMenuStatus(true)
      return
    }

    if (menuType == 'Trade') {
      //changing the menu according to module
      setstaticMenuList(tradeMenu)
      //setting module name to show as titile
      setsubMenuName(menuType)
      //setting state to manage show / hide title at top
      setsubMenuStatus(true)
      return
    }
    if (menuType == 'Water') {
      //changing the menu according to module
      setstaticMenuList(waterMenu)
      //setting module name to show as titile
      setsubMenuName(menuType)
      //setting state to manage show / hide title at top
      setsubMenuStatus(true)
      return
    }
  }


  const menuPermission = [
    // ============================================= 1 Property Menu=============================================
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Dashboard', menuPath: '/landingDashboard', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Property Dashboard', menuPath: '/propertyDashboard', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Property Apply', menuPath: '/safform/new/0', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Water Dashboard', menuPath: '/water', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Trade Dashboard', menuPath: '/trade', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Trade New License', menuPath: '/trade-new-apply', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Trade Renewal', menuPath: '/trade-renewal', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Trade Amendment', menuPath: '/trade-amendment', subMenu: [] },
    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Trade Surrender', menuPath: '/trade-surrender', subMenu: [] },

    { icon: <FcFlowChart size={14} />, module: 'property', id: `property_1`, menuName: 'Payment-History', menuPath: '/propertyTranscation', subMenu: [] },
  ]


  const expandSidebar = () => {
    props.navOriginalCloseStatus && props.NAV_OPEN() //if shorthand
  }
  const contractSidebar = () => {
    props.navOriginalCloseStatus && props.NAV_CLOSE() //if shorthand
  }


  return (
    <>
      {
        ((location.pathname != '/landing') && (location.pathname != '/') && (location.pathname != '/error')) && <div style={{ 'zIndex': '1000' }} className={"w-full flex shadow-lg  text-gray-700 transition-all z-50 border-r-2 scbar animate__animated animate__fadeInLeft bg-gray-200"} onMouseEnter={expandSidebar} onMouseLeave={contractSidebar}>
          {/* ((location.pathname != '/landing') && (location.pathname != '/') && (location.pathname != '/error')) && <div style={{'zIndex':'1000'}} className={(props.navCloseStatus ? "w-0 sm:w-9" : "w-56 pr-2") + " shadow-lg px-0  h-screen pb-12 overflow-y-scroll absolute top-11 md:top-52 left-0  text-gray-700 transition-all z-50 border-r-2 scbar animate__animated animate__fadeInLeft bg-white"} onMouseEnter={expandSidebar} onMouseLeave={contractSidebar}> */}
          {/* <div className='w-full  h-full bg-black absolute top-0 left-0 opacity-60'> </div> */}
          {/* <div className='py-4' >
            <ImageCard sideBarStatus={props.navCloseStatus} />
          </div> */}

          <div className='flex mx-auto h-8 overflow-hidden items-center'>
            {subMenuStatus && <div className='flex-initial px-3 ml-2' style={{ 'zIndex': '1000' }}>
              <div className='h-full w-full flex justify-center items-center text-white font-semibold cursor-pointer'><span onClick={() => menuChange('Back')} className='px-4  hover:bg-white bg-gray-700 hover:text-gray-700 cursor-pointer'>{'<<'}</span></div>
            </div>}
            <div className="flex-initial">
              <ul className={`list-none  flex`} style={props.navCloseStatus == true ? { 'padding': '0px 2px 0px 2px' } : { 'padding': '0px 10px 0px 10px' }}>
                {staticMenuList != '' && staticMenuList?.map((data, index) => (
                  <CollapseMenu menuChange={menuChange} key={`sidebar_menu${index}`} title={data.menuName} path={data.menuPath} subMenu={[]} subMenuStatus={false} icon={<FcFlowChart size={14} />} />
                ))}
              </ul>
            </div>
          </div>
        </div>
      }

    </>
  )
}

//for redux
const mapStateToProps = (state) => {
  return {
    navCloseStatus: state.navCloseStatus,
    navOriginalCloseStatus: state.navOriginalCloseStatus
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    // buyCake: () => dispatch(buyCake())
    NAV_OPEN: (data2) => dispatch({ type: "NAV_OPEN" }),
    NAV_CLOSE: (data3) => dispatch({ type: "NAV_CLOSE" })
  };
};
// export default Home // EXPORTING HOME COMPONENT
export default connect(mapStateToProps, mapDispatchToProps)(Sidebar);

